.Deck-btn {
	padding: 1rem 2rem;
	font-size: 1rem;
	font-weight: bold;
	color: #bfec1d;
	border: 2px solid #bfec1d;
	background-color: rgba(0, 0, 0, 0);
	border-radius: 5px;
	cursor: pointer;
}

.Deck-btn:hover {
	background-color: #bfec1d;
	color: #282c34;
}

.Deck-container {
	padding: 15px;
	position: relative;
}
